#nlp-search .search-bar {
  width: 100%;
  background: #ffffff;
  min-height: 85px;
  padding: 20px;
  display: flex;
  align-items: center; }

#nlp-search .search-title {
  display: inline-block;
  padding: 0 20px;
  white-space: nowrap;
  font-size: 18px; }

#nlp-search .search-button {
  display: flex;
  padding: 7px 25px 4px;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
  border: 1px solid #8ce4c6;
  border-radius: 6px;
  color: #46be8a;
  background-color: #ffffff; }
  #nlp-search .search-button .form-control-feedback {
    position: initial;
    display: inline-block; }

#nlp-search .form-control {
  border: none;
  box-shadow: none;
  margin-right: 40px;
  font-size: 18px;
  padding: 0;
  border-radius: 0;
  border-bottom: 1px solid #d7d7d7; }
